<template>
  <div class="loginBox" @keyup.enter="login">
    <div class="loginTitle">
      <h2>CERT</h2>
      <h3>IT</h3>
    </div>
    <div class="inputLine">
      <input type="text" placeholder="" v-model="account" ref="account">
    </div>
  
    <div class="inputLine">
      <input type="password" placeholder="" v-model="password" ref="password" :class="input" @keyup="changeErr">
    </div>
    <div class="checkLine" v-show="!validation">
      <span class="inputErr"></span>
    </div>
    <div class="checkLine">
      <span class="checkboxBorder" @click="select">
        <span :class="checkboxInside">
        </span>
        <input type="checkbox" class="rememberMe" v-model="remember">
      </span>
      <span class="checkboxWord"></span>
    </div>
    <div class="btnLine">
      <button class="btnLogin" @click="login"></button>
      <button class="btnRegist" @click="register"></button>
    </div>
  </div>
</template>

<style lang="stylus">
  @import "../assets/styles/common.styl"
  .loginBox
    border-radius: 5px
    -moz-border-radius: 5px
    background-clip: padding-box
    margin: 0px auto
    width: 350px
    padding: 15px 35px 15px
    background: #fff
    border: 1px solid #eaeaea
    box-shadow: 0 0 25px #cac6c6
    font-size: fontSize * 1.3

  .loginTitle
    font-size: fontSize * 1.6
    padding-bottom: 15px
    h2
      text-shadow: 0px 0px 5px rgba(106, 210, 119, 0.37)

  .btnRegist
    background-color: rgba(106, 210, 119, 1) 
    &:hover
      background-color: #4dc35c
      border-color: #4dc35c

  .checkboxBorder,.checkboxInside,.inside
    cursor: pointer
    display: inline-block
    position: relative
    white-space: nowrap

  .rememberMe
    opacity 0
  .checkboxBorder
    width 18px
    height 20px
    outline 0
    border:1px solid btnColor
    border-radius: 3px
    margin-right: 5px 
    line-height: 1;
    vertical-align: middle;
  .checkboxInside
    width 18px
    height 20px
    background-color: btnColor
    &:after
      content:""
      display:block
      margin: 3px auto
      width:4px
      height: 8px
      border-right:2px white solid
      border-bottom: 2px white solid
      transform: rotateZ(50deg)
  .checkboxWord
    font-size: fontSize * 1.1
    vertical-align: middle
    font-weight: bold

    

</style>
<script>
import { mapGetters, mapState } from 'vuex'
export default {
  name: 'login',
  data() {
    return {
      account: '', //
      password: '', // 
      input: '',     //input class
      remember: false, //
      checkboxInside: 'inside', //checkbox 
      validation: true, //
      focusStatus: false, //,
      loginApi: 'http://lj661.cn:8000/login/',//URL
      isLoginApi: "http://lj661.cn:8000/isLogin/"//URL
    }
  },
  methods: {
    validate(data) { // 
      if (data.status == 200) { //200
        this.$store.commit("isLogin", { name: data.name, token: data.token });
        window.localStorage.setItem("token", data.token); //localStroage token
        window.localStorage.setItem("user", data.name); //localStroage user
        this.$router.push({ path: "/famous" })
      } else if (data.status == 404) {//404
        this.validation = false
        this.input = "inputErr"
        this.focusStatus = true
        this.$refs.password.focus(this.focusStatus);
      }
    },
    changeErr() {//input 
      this.focusStatus = true
      if (this.focusStatus) {
        this.input = ""
        this.validation = true
      }
    },
    login() { //
      let pwd = this.password
      let account = this.account
      let time;
      if (this.remember) {
        time = new Date().getTime() + 7 * 3600 * 24 * 1000 // token   30
      } else {
        time = new Date().getTime() + 3600 * 0.5 * 1000 // token 30
      }

      this.$http.post(this.loginApi, { //
        username: account,
        password: pwd,
        remember: this.remember,
        "time": time
      }, {}).then((response) => {//
        this.validate(response.data)
      })
    },
    select() { //cookie
      this.checkboxInside = (this.checkboxInside == "inside") ? "checkboxInside" : "inside" //checkboxInside Class
      this.remember = (this.remember) ? false : true //remember
    },
    register() {
      this.$router.push({ path: "/Register" })
    }
  },
  create() {
    if (window.localStorage.getItem("token")) { //createtoken
      this.$http.post(this.isLoginApi, { token: window.localStorage.getItem("token") }).then((res) => {
        this.validate(res.body)
      })
    }

  }
}
</script>



