import Vue from 'vue' //Vue 
import Router from 'vue-router' //
import Login from '@/components/Login.vue'  //login 
import Hello from '@/common/home.vue'  //home 
import Register from '@/components/Register.vue'  //Register 
import Famous from '@/components/famous.vue'
import Works from '@/components/Works.vue'
import Contact from '@/components/contact.vue'
import Advice from '@/components/advice.vue'
import Photo from '@/components/photo.vue'
import Fresh from "@/components/fresh.vue"
Vue.use(Router) 

export default new Router({
  routes: [ //
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/Hello/:userID', 
      name: 'HelloID',
      meta: {
          // 
            requireAuth: true,  
          },           
      component: Hello

    },
    {
      path: '/Hello/', 
      name: 'Hello',
      meta: {
          // 
            requireAuth: true,  
          },           
      component: Hello

    },
    {
      path: '/Register', 
      name: 'Register',        
      component: Register
    },
    {
      path: "/famous",
      name:"famous",
      component:Famous
    },
    {
      path: "/Works",
      name:"Works",
      component: Works
    },
    {
      path: "/advice",
      name:"advice",
      component: Advice
    },
    {
      path: "/contact",
      name:"contact",
      component: Contact
    },
    {
      path:"/photo",
      name:"photo",
      component:Photo
    },
    {
      path:"/fresh",
      name:"fresh",
      component:Fresh
    }

  ]
})



